import React from "react"
import "./style.css";
import PricingTable from "../../elements/pricingtable/table";
import {Fade} from "react-reveal";
import translate from "../../translate"

const PricingComponent = ({noanimation, pricings}) => {

    var TagName = noanimation ? 'div' : Fade;
    return <div className={"pricingcomponent"}>

        {pricings.length < 2 && <div/>}


        {pricings.map((pricing, idx) => {

            return <TagName className={"pricingcomopwrap"} left={idx === 0} right={idx === 2}>
                <PricingTable title={pricing.title}
                              price={pricing.price}
                              project={pricing.project}
                              subtitle={pricing.subtitle}
                              button={pricing.button}
                              prices={pricing.includes}/>
            </TagName>
        })}


    </div>

};

export default PricingComponent;
