import React from "react"
import "./style.css"
import PlayOverlay from "../play/play"

function throttle(callback, limit) {

    var wait = false;
    var cnt = 0;
    return function () {
        if (!wait) {
            cnt += 1;
            callback.apply(null, [...arguments, cnt, limit]);
            wait = true;
            setTimeout(function () {
                wait = false;
            }, limit);
        }
    }
}

function once(fn, context) {
    var result;

    return function () {
        if (fn) {
            result = fn.apply(context || this, arguments);
            fn = null;
        }

        return result;
    };
}


const Video = ({project, preload}) => {


    // Call the debounced function on every mouse move
    const th = throttle(function (e, video, cnt, delay) {
        var duration = (cnt * delay) / 1000 / 60;
        console.log(`Video ${video} reachted ${duration}`);
        window.gtag && window.gtag('event', 'video_running', {
            'event_category': `video_${video}`,
            'event_label': `Reached ${cnt * delay / 1000} Seconds`,
            'value': duration
        });
    }, 5000);

    var playOnce = once(function (video) {
        console.log(`Video ${video} played`);
        window.gtag && window.gtag('event', 'video_play', {
            'event_category': 'video',
            'event_label': video
        });
    });

    return <div className={"video_wrapper"} onClick={() => {

        if (!!document.getElementById(`${project}-play`)) {
            document.getElementById(project).setAttribute("controls", "controls");
            document.getElementById(project).play();
            document.getElementById(`${project}-play`).remove();
            document.getElementById(`${project}-over`).remove();
        }


    }}>


        <PlayOverlay id={project}>
            <video onTimeUpdate={e => th(e.target.currentTime, project)}
                   onPlay={e => playOnce(project)}
                   preload={preload} id={project} poster={require(`../../projekt/${project}/${project}.jpg`)}
                   style={{"width": "100%", "height": "auto"}}>
                <source src={require(`../../projekt/${project}/${project}.mp4`)} type="video/mp4"/>
            </video>
        </PlayOverlay>


    </div>
}

export default Video