import React from "react"
import "./style.css"

const PlayOverlay = ({ onClick, person, style, showPause, children, id, className, reverse }) => (
  <div onClick={onClick} className={"playoverlay"}>
    <div id={`${id}-over`} className={"overlayvideo"}/>
    <div className={"playchildren"}>
      {children}
    </div>
    {!showPause && <img alt="play video icon" id={`${id}-play`} className={"playiconimg"} src={require("./playicon.svg")}/>}
    {showPause && <img alt="play video icon" id={`${id}-play`} className={"playiconimg"} src={require('./pause.png')}/>}
  </div>
)

export default PlayOverlay
