import React from "react"
import "./style.css"
import BlockModern from "../../elements/blockmodern/style"
import { Zoom } from "react-reveal"


const PromisesComponent = ({ promises }) => {
  return <div className={"promises"}>
    {promises.map((element, idx) => {
      return <Zoom delay={300 * idx}>
        <BlockModern title={element.title}
                     img={require(`../../images/feature-icon-0${idx+1}.svg`)}
                     alt={`icon ${element.title}`}
                     subtitle={element.description}/>
      </Zoom>
    })}
  </div>
}

export default PromisesComponent
