import React from "react"
import './style.css';
import Block from "../block/block";
import Text from "../text/text";
import Button from "../button/button";
import BlockContent from "../block/content";

const Quote = ({text, href, children, className, style, author,path}) => (
    <Block className={"quotewrapper"} style={{"width": 'initial', 'maxWidth': 'initial'}}>
        <BlockContent className={"quote"}>
            <img src={require('./../../images/webworkers.png')}/>
            <Text className={"quotebody"}>{text}</Text>
            {path && <Button href={path}>Mehr über's Projekt</Button>}
            <p className={"author"}>{author}</p>
        </BlockContent>

    </Block>
);

export default Quote
