import React from "react"
import "./style.css"
import Text from "../text/text"

const LaptopBox = ({ title, text, left, img }) => (
  <div className={`laptopbox ${left ? "leftbox" : "rightbox"}`}>
    <div>
      <h2>{title}</h2>
      <Text>{text}</Text>
    </div>
    <img alt={title} src={img}/>
  </div>
)

export default LaptopBox
