import React from "react"
import Video from "../video/video";

const Thumbnail = ({projekt}) => {
    try {
        require(`./../../projekt/${projekt}/${projekt}.mp4`);
        return <Video preload={"none"} project={projekt}/>;
    } catch (err) {
        return <img alt="blog thumbnail" src={require(`./../../projekt/${projekt}/${projekt}.jpg`)}/>;
    }
};


export default Thumbnail;
