import React from "react"
import "./style.css"
import Block from "../block/block"
import Text from "../text/text"
import classnames from "classnames"
import PlayOverlay from "../play/play"
import translate from "../../translate"
import BlockContent from "../block/content";
import {Zoom} from "react-reveal";

class Person extends React.Component {
    state = {
        showPause: false,
    }

    render() {
        const {person, text, onClick, style, hasAudio, children, className, reverse} = this.props;
        console.log(person);
        return (
            <Zoom>
                <Block
                    className={classnames("person", className, reverse ? "reverseperson" : "")} style={style}>
                    <BlockContent className={"personwrapper"}>
                        {hasAudio && <PlayOverlay showPause={this.state.showPause} onClick={() => {
                            this.setState({showPause: !this.state.showPause})
                            console.log(this.state)
                            onClick()
                        }} id={person.name}>
                            <img className={"personimage"} alt={"Mitarbeiter " + person.name}
                                 src={require(`../../images/person/${person.image}`)}/>
                        </PlayOverlay>}

                        {!hasAudio && <img className={"personimage"} alt={"Mitarbeiter " + person.name}
                                           src={require(`../../images/person/${person.image}`)}/>}
                        <div className="name">{person.name}</div>
                        <Text>{text ? text : person.description}</Text>

                        <div className="footer">
                            {person.email && <Text><a
                                href={"mailto:" + person.email}>{person.email}</a></Text>}
                            {(person.phone !== "-" && !!person.phone) &&
                            <Text><a className="mt-8" href={"tel:" + person.phone.replace(/ /g, "")}>{person.phone}</a></Text>}
                            {children}
                        </div>
                    </BlockContent>
                </Block>
            </Zoom>
        )
    }
}

export default Person
